<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow p-3 rounded-lg">
              <p class="p-2 text-lg font-semibold"><span class="typcn typcn-pen"></span> FORM UNDANGAN</p>
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
          <div class="table-responsive">
              <table class="table table-sm table-striped table-bordered">
                  <tr>
                      <td colspan="3">
                          <p class="p-2 font-semibold text-lg">Data Mempelai Pria : </p>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">nama_lengkap</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="text" id="nama_lengkap1" name="nama_lengkap1" class="form-control form-control-sm" placeholder="nama_lengkap1" v-model="vdata.nama_lengkap1" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">nama_singkat</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="text" id="nama_singkat1" name="nama_singkat1" class="form-control form-control-sm" placeholder="nama_singkat1" v-model="vdata.nama_singkat1" >
                          </div>
                      </td>
                  </tr>
                     <tr>        
                        <td class="capitalize">keluarga</td>
                        <td class="pl-2 pr-2">:</td>
                        <td>
                            <div class="sm-form ">
                                <input type="text" id="keluarga1" name="keluarga1" class="form-control form-control-sm" placeholder="keluarga1" v-model="vdata.keluarga1" >
                            </div>
                        </td>
                    </tr>
                     <tr>        
                        <td class="capitalize">gambar</td>
                        <td class="pl-2 pr-2">:</td>
                        <td>
                            <div class="sm-form ">
                                <input type="file" id="filenya" name="gambar_pria" class="form-control form-control-sm" placeholder="gambar_pria" @change="upload1" >
                                <input type="text" id="gambar_pria" name="gambar_pria" class="form-control form-control-sm" placeholder="gambar_pria" v-model="vdata.gambar_pria" >
                                <img style="width:100px" v-viewer :src="vdata.gambar_pria" v-if="vdata.gambar_pria" alt="">
                            </div>
                        </td>
                    </tr>
                     <tr>        
                        <td class="capitalize">ucapan singkat</td>
                        <td class="pl-2 pr-2">:</td>
                        <td>
                           <div class="sm-form">
                           <textarea type="text" id="ucapan1" name="ucapan1" rows="2" placeholder="ucapan1..." class="form-control md-textarea" v-model="vdata.ucapan1"
                           ></textarea>
                           </div>
                        </td>
                    </tr>
                    <!-- ================================================== -->
                    <tr>
                      <td colspan="3">
                          <p class="p-2 font-semibold text-lg">Data Mempelai Wanita : </p>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">nama_lengkap</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="text" id="nama_lengkap2" name="nama_lengkap2" class="form-control form-control-sm" placeholder="nama_lengkap2" v-model="vdata.nama_lengkap2" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">nama_singkat</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="text" id="nama_singkat2" name="nama_singkat2" class="form-control form-control-sm" placeholder="nama_singkat2" v-model="vdata.nama_singkat2" >
                          </div>
                      </td>
                  </tr>
                     <tr>        
                        <td class="capitalize">keluarga</td>
                        <td class="pl-2 pr-2">:</td>
                        <td>
                            <div class="sm-form ">
                                <input type="text" id="keluarga2" name="keluarga2" class="form-control form-control-sm" placeholder="keluarga2" v-model="vdata.keluarga2" >
                            </div>
                        </td>
                    </tr>
                      <tr>        
                        <td class="capitalize">gambar</td>
                        <td class="pl-2 pr-2">:</td>
                        <td>
                            <div class="sm-form ">
                                <input type="file" id="filenya2" name="gambar_wanita" class="form-control form-control-sm" placeholder="gambar_wanita" @change="upload2" >
                                <input type="text" id="gambar_wanita" name="gambar_wanita" class="form-control form-control-sm" placeholder="gambar_wanita" v-model="vdata.gambar_wanita" >
                                <img style="width:100px" v-viewer :src="vdata.gambar_wanita" v-if="vdata.gambar_wanita" alt="">
                            </div>
                        </td>
                    </tr>
                     <tr>        
                        <td class="capitalize">ucapan singkat</td>
                        <td class="pl-2 pr-2">:</td>
                        <td>
                           <div class="sm-form">
                           <textarea type="text" id="ucapan2" name="ucapan2" rows="2" placeholder="ucapan2..." class="form-control md-textarea" v-model="vdata.ucapan2"
                           ></textarea>
                           </div>
                        </td>
                    </tr>
                    <!-- ================================================== -->
              </table>
          </div>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow rounded-lg">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
let urlnya ='https://undangan.officialapp.website'
export default {
layout:'app_admin_undangan',
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
    };
  },
  methods: {
    upload1(){
      let file = document.querySelector("#filenya"); // berikan id pada input file
      sdb.collection('tbuser',false,urlnya).upload(file,true,true) // file,compress,scramble
      .then(res=>{
        this.vdata.gambar_pria=res;
        this.$forceUpdate();
      })
    },
    upload2(){
      let file = document.querySelector("#filenya2"); // berikan id pada input file
      sdb.collection('tbuser',false,urlnya).upload(file,true,true) // file,compress,scramble
      .then(res=>{
        this.vdata.gambar_wanita=res;
        this.$forceUpdate();
      })
    },
    getData(){
      sdb.collection("app_undangan_nikah_mempelai",false,urlnya).doc().select(`select * from app_undangan_nikah_mempelai`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          await sdb
            .collection("app_undangan_nikah_mempelai",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "update") {
         await sdb
            .collection("app_undangan_nikah_mempelai",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_undangan_nikah_mempelai",true,urlnya)
            .doc(this.vdata.id)
            .delete()
              that.getData();
        }
      }
    },
  },
  async mounted() {
          this.getData();
  },
};
</script>